import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { PasswordInput, Input, Button } from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "../pages.module.css";
import { motion } from "framer-motion";
import { fetchResetPassword } from "../../utils/api";
import { forgotPasswordSucces } from "../../services/actions/auth-actions";

export const ResetPasswordPage = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [password, setPassword] = React.useState("");
  const [token, setToken] = React.useState("");

  const email = useSelector((state) => state.authStore.email);

  useEffect(() => {
    if (!email) {
      navigate("/forgot-password");
    }
  }, [email]);

  const handleSubmit = (e) => {
    e.preventDefault();
    fetchResetPassword(password, token);
    navigate("/login");
    dispatch(forgotPasswordSucces(false));
  };

  return (
    <section className={styles.box}>
      <form className={styles.container} onSubmit={handleSubmit}>
        <motion.h1
          className="text text_type_main-medium mb-6"
          // 
          initial={{ x: "-100%", opacity: 0 }}
          animate={{ x: "0", opacity: 1 }}
          transition={{ ease: "easeOut", duration: 1 }}
        >
           
        </motion.h1>
        <motion.div
          // 
          initial={{ x: "100%", opacity: 0 }}
          animate={{ x: "0", opacity: 1 }}
          transition={{ ease: "easeOut", duration: 1.5 }}
        >
          <PasswordInput
            name={"password"}
            type={"text"}
            placeholder={"  "}
            onChange={(e) => setPassword(e.target.value)}
            icon={"ShowIcon"}
            value={password}
            errorText={"  "}
            size={"default"}
            extraClass="mb-6"
            required
          />
        </motion.div>
        <motion.div
          // 
          initial={{ x: "-100%", opacity: 0 }}
          animate={{ x: "0", opacity: 1 }}
          transition={{ ease: "easeOut", duration: 1.5, delay: 0.5 }}
        >
          <Input
            name={"token"}
            type={"text"}
            placeholder={"   "}
            onChange={(e) => setToken(e.target.value)}
            value={token}
            errorText={"    "}
            size={"default"}
            extraClass="mb-6"
            required
          />
        </motion.div>
        <motion.div
          // 
          initial={{ y: "200%", opacity: 0 }}
          animate={{ y: "0", opacity: 1 }}
          transition={{ ease: "easeOut", delay: 1, duration: 1 }}
        >
          <Button
            htmlType="submit"
            type="primary"
            size="medium"
            extraClass="mb-20"
            disabled={password && token ? false : true}
          >
            
          </Button>
        </motion.div>
        <motion.p
          className="text text_type_main-default text_color_inactive"
          // 
          initial={{ y: "200%", opacity: 0 }}
          animate={{ y: "0", opacity: 1 }}
          transition={{ ease: "easeOut", delay: 1.5, duration: 1.5 }}
        >
           ?&nbsp;
          <Link
            to={"/login"}
            className={"text text_type_main-default " + styles.link}
          >
            
          </Link>
        </motion.p>
      </form>
    </section>
  );
};
