import React from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { PasswordInput, EmailInput, Button } from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "../pages.module.css";
import { motion } from "framer-motion";
import { loginAction } from "../../services/actions/auth-actions";

export const LoginPage = () => {
  const dispatch = useDispatch();

  const [email, setEmail] = React.useState("");
  const [password, setPassword] = React.useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(loginAction(email, password));
  };

  return (
    <section className={styles.box}>
      <form className={styles.container} onSubmit={handleSubmit}>
        <motion.h1
          className="text text_type_main-medium mb-6"
          // 
          initial={{ y: "-200%", opacity: 0 }}
          animate={{ y: "0", opacity: 1 }}
          transition={{ ease: "easeOut", delay: 1, duration: 1 }}
        >
          
        </motion.h1>
        <motion.div
          // 
          initial={{ x: "100%", opacity: 0 }}
          animate={{ x: "0", opacity: 1 }}
          transition={{ ease: "easeOut", duration: 1.5 }}
        >
          <EmailInput
            name={"email"}
            type={"email"}
            placeholder={"E-mail"}
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            errorText={"   "}
            isIcon={false}
            extraClass="mb-6"
            required
          />
        </motion.div>
        <motion.div
          // 
          initial={{ x: "-200%", opacity: 0 }}
          animate={{ x: "0", opacity: 1 }}
          transition={{ ease: "easeOut", duration: 1.5 }}
        >
          <PasswordInput
            name={"password"}
            type={"text"}
            placeholder={""}
            onChange={(e) => setPassword(e.target.value)}
            icon={"ShowIcon"}
            value={password}
            errorText={"  "}
            size={"default"}
            extraClass="mb-6"
            required
          />
        </motion.div>
        <motion.div
          // 
          initial={{ y: "200%", opacity: 0 }}
          animate={{ y: "0", opacity: 1 }}
          transition={{ ease: "easeOut", delay: 1, duration: 1 }}
        >
          <Button
            htmlType="submit"
            type="primary"
            size="medium"
            extraClass="mb-20"
            disabled={email && password ? false : true}
          >
            
          </Button>
        </motion.div>
        <motion.p
          className="text text_type_main-default text_color_inactive mb-4"
          // 
          initial={{ y: "200%", opacity: 0 }}
          animate={{ y: "0", opacity: 1 }}
          transition={{ ease: "easeOut", delay: 1, duration: 1.5 }}
        >
            ?&nbsp;
          <Link
            to={"/register"}
            className={"text text_type_main-default " + styles.link}
          >
            
          </Link>
        </motion.p>
        <motion.p
          className="text text_type_main-default text_color_inactive"
          // 
          initial={{ y: "200%", opacity: 0 }}
          animate={{ y: "0", opacity: 1 }}
          transition={{ ease: "easeOut", delay: 1.5, duration: 1.5 }}
        >
           ?&nbsp;
          <Link
            to={"/forgot-password"}
            className={"text text_type_main-default " + styles.link}
          >
             
          </Link>
        </motion.p>
      </form>
    </section>
  );
};
